<template>
  <v-app>
    <v-main>
      <loading />
      <v-app-bar
        color="negcovid"
        dense
        dark
        shrink-on-scroll
        scroll-target="#scrolling-techniques-2"
        :height="height"
      >
        <template v-slot:extension>
          <v-tabs v-model="tab" align-with-title>
            <v-tab>Dashboard</v-tab>
            <v-tab>User Management</v-tab>
          </v-tabs>
        </template>
        <v-toolbar-title>
          <div
            v-if="this.$route.name === 'Main'"
            style="font-size: 30px; font-weight: bold"
            v-bind:class="[
              $vuetify.breakpoint.name === 'xs' ? xsClass : otherClass,
            ]"
          >
            Dashboard
          </div>
          <div
            v-if="this.$route.name === 'User_management'"
            style="font-size: 25px; font-weight: bold"
            v-bind:class="[
              $vuetify.breakpoint.name === 'xs' ? xsClass : otherClass,
            ]"
          >
            {{ this.$store.state.place }}
          </div>
        </v-toolbar-title>

        <v-spacer></v-spacer>
        <v-row class="pt-5 px-2">
          <v-col cols="12" class="pl-0 ml-0">
            <v-row class="d-flex align-center justify-end">
              <div
                v-if="$vuetify.breakpoint.name !== 'xs'"
                class="pr-5"
                style="font-size: 17px"
              >
                {{ this.profile.email }}
              </div>
              <v-avatar size="50" class="stokeImgMe">
                <v-img :src="this.profile.profilepicture"> </v-img>
              </v-avatar>
            </v-row>
          </v-col>
          <v-col class="pa-0 ma-0" v-if="$vuetify.breakpoint.name === 'xs'">
            <v-row justify="end" class="px-3 py-3">
              <div style="font-size: 17px">
                {{ this.profile.email }}
              </div>
            </v-row>
          </v-col>
        </v-row>
      </v-app-bar>
      <router-view
        v-if="
          $vuetify.breakpoint.name === 'xs' || $vuetify.breakpoint.name === 'sm'
        "
      />
      <Maindesktop
        v-if="
          ($vuetify.breakpoint.name === 'md' && tab === 0) ||
          ($vuetify.breakpoint.name === 'lg' && tab === 0)
        "
      />

      <v-bottom-navigation
        app
        absolute
        color="primary"
        grow
        class="fix-bottom"
        v-if="$vuetify.breakpoint.name === 'xs'"
      >
        <v-btn v-for="(m, i) in menu" :key="i" @click="goPage(m.path, i)">
          <span :class="active == i ? 'primary--text' : 'greyC--text'">{{
            m.title
          }}</span>
          <span
            class="icon"
            :class="active == i ? 'icon-active ' + m.icon : m.icon"
          ></span>
        </v-btn>
      </v-bottom-navigation>
    </v-main>
  </v-app>
</template>

<script>
import Maindesktop from "./views/main_desktop.vue";
import loading from "@/components/Loading";
export default {
  components: {
    Maindesktop,
    loading,
  },
  data() {
    return {
      place: "",
      drawer: false,
      txt: "",
      profile: "",
      xsClass: "pl-0",
      otherClass: "pl-10",
      active: 0,
      tab: 0,
      menu: [
        {
          title: "Dashboard",
          // icon: "icon-home",
          path: "/",
        },
        {
          title: "User management",
          // icon: "icon-timeline",
          path: "/User_management",
        },
      ],
    };
  },
  async mounted() {
    await this.get_profile();
  },
  methods: {
    async get_profile() {
      await this.axios
        .get(this.$store.state.path_api + "profile", {
          headers: this.$store.state.path_header,
        })
        .then((result) => {
          this.profile = result.data.result.profile.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    goPage(path, i) {
      localStorage.setItem("active", i);
      this.active = i;
      this.$router.push(path);
    },
  },
  computed: {
    height() {
      if (this.$vuetify.breakpoint.name == "xs") {
        return 90;
      }
      return 70;
    },
    loading() {
      return this.$store.state.loading;
    },
  },
  watch: {
    loading(newValue) {
      this.$nextTick(() => {
        if (newValue === false) {
          // this.$store.commit("getloading", false);
        }
      });
    },
    tab(newValue) {
      if (newValue === 1) {
        console.log(777);
        this.$router.push("/User_management");
      }
    },
  },
};
</script>

<style scoped>
.stokeImgMe {
  border: 2px solid #fff;
}
.fix-bottom {
  position: fixed !important;
  left: 0;
  bottom: 0;
  width: 100%;
}
</style>
