<template>
  <div>
    <v-container grid-list-xs>
      <v-card elevation="2" class="ma-2" outlined>
        <v-container grid-list-xs>
          <v-row>
            <v-col>
              <v-container grid-list-xs>
                <v-row justify="end" align="start" class="pl-5">
                  <div class="font-weight-black" style="font-size: 20px">
                    Access Person :
                  </div>
                </v-row>
                <v-row align="end" class="px-3">
                  <div style="font-size: 15px; color: #777777">
                    {{ today }}
                  </div>
                  <v-spacer></v-spacer>
                  <div
                    class="font-weight-black"
                    style="font-size: 50px; color: #ffa62b"
                  >
                    {{ mainStatus_list[0].total_case }}
                  </div>
                </v-row>
              </v-container>
              <v-row justify="center" class="ma-2">
                <v-divider class="ma-0"></v-divider>
              </v-row>
              <v-row class="mx-1">
                <v-col cols="7" class="px-0">
                  <apexchart
                    class="ml-n8"
                    width="215"
                    type="donut"
                    :options="options"
                    :series="series"
                  ></apexchart>
                </v-col>
                <v-col cols="5" align-self="center">
                  <v-row justify="start">
                    <div class="font-weight-black negcovid--text">Green :</div>
                  </v-row>
                  <v-row justify="end">
                    {{ this.mainStatus_list[0].green_case }} ({{
                      this.mainStatus_list[0].g_percent
                    }}%)</v-row
                  >
                  <v-row justify="start">
                    <div class="font-weight-black pluscovid--text">Red :</div>
                  </v-row>
                  <v-row justify="end">
                    {{ this.mainStatus_list[0].red_case }} ({{
                      this.mainStatus_list[0].r_percent
                    }}%)
                  </v-row>
                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </v-container>
      </v-card>
      <v-card outlined>
        <v-tabs
          v-if="
            $vuetify.breakpoint.name == 'xs' || $vuetify.breakpoint.name == 'sm'
          "
          fixed-tabs
          color="primary"
          height="40px"
          v-model="tab"
          centered
        >
          <v-tab>
            <span>Summary</span>
          </v-tab>
          <v-tab>
            <span>History</span>
          </v-tab>
        </v-tabs>
        <Summary v-if="tab === 0" />
        <History v-if="tab === 1" />
      </v-card>
    </v-container>
  </div>
</template>

<script>
import Summary from "../components/summary.vue";
import History from "../components/history.vue";
export default {
  components: {
    Summary,
    History,
  },
  data() {
    return {
      tab: 0,
      options: {
        chart: {
          type: "donut",
        },
        noData: {
          text: "Loading...",
        },
      },
      series: [],
      mainStatus_list: [],
      today: Date().toString().slice(0, 15),
    };
  },
  methods: {
    async get_main_data() {
      await this.axios
        .get(this.$store.state.path_api + "stat", {
          headers: this.$store.state.path_header,
        })
        .then(async (result) => {
          for (const item of result.data.result) {
            this.mainStatus_list.push(item);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    async updateChart() {
      this.options = {
        labels: ["Green", "Red"],
        fill: {
          type: "gradient",
          gradient: {
            shade: "light",
            type: "horizontal",
            shadeIntensity: 0.2,
            inverseColors: false,
          },
        },
        dataLabels: {
          enabled: false,
        },
        legend: {
          show: false,
        },
        colors: [
          this.$vuetify.theme.themes.light.negcovid,
          this.$vuetify.theme.themes.light.pluscovid,
        ],
      };
      this.series = [
        this.mainStatus_list[0].green_case,
        this.mainStatus_list[0].red_case,
      ];
    },
  },
  async mounted() {
    this.$nextTick(async () => {
      await this.get_main_data();
      await this.updateChart();
      this.$store.commit("setloading", false);
    });
  },
};
</script>

<style scoped>
.test {
  width: 100% !important;
}
</style>
