<script>
import Summary from "../components/summary.vue";
import History from "../components/history.vue";
export default {
  components: {
    Summary,
    History,
  },
  data() {
    return {
      options: {
        chart: {
          type: "donut",
        },
        noData: {
          text: "Loading...",
        },
      },
      series: [],
      mainStatus_list: null,
      today: Date().toString().slice(0, 15),
    };
  },
  methods: {
    async get_main_data() {
      await this.axios
        .get(this.$store.state.path_api + "stat", {
          headers: this.$store.state.path_header,
        })
        .then((result) => {
          this.mainStatus_list = result.data.result;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    async updateChart() {
      this.options = {
        labels: ["Green", "Red"],
        fill: {
          type: "gradient",
          gradient: {
            shade: "light",
            type: "horizontal",
            shadeIntensity: 0.2,
            inverseColors: false,
          },
        },
        dataLabels: {
          enabled: false,
        },
        legend: {
          show: false,
        },
        colors: [
          this.$vuetify.theme.themes.light.negcovid,
          this.$vuetify.theme.themes.light.pluscovid,
        ],
      };
      this.series = [
        this.mainStatus_list[0].green_case,
        this.mainStatus_list[0].red_case,
      ];
    },
  },
  mounted() {
    this.$nextTick(async () => {
      await this.get_main_data();
      await this.updateChart();
      this.$store.commit("setloading", false);
    });
  },
};
</script>

